.cates {
    display: flex;
    flex-wrap: wrap;
}
.categorie {
    flex: 1 50%;
}
.cate {
    margin: 4px;
    background: var(--color-primary);
    display: flex;
    align-items: center;
    border-radius: 4px;
    overflow: hidden;
    cursor: pointer;
    transition: 0.3s;
}
.cate:hover {
    opacity: 0.75;
}
.cticon {
    padding: 10px;
    background: #8f81ff;
    color: #22177e;
}
.ct {
    flex: 1;
}
.ct h4 {
    color: var(--color-light);
    text-align: left;
    margin-left: 10px;
    font-size: 15px;
    font-weight: var(--font-weight-normal);
}
