.errormain {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 100px 0;
}
.error404 {
  display: flex;
  width: 60%;
}
.errorleft {
  flex: 1 50%;
  display: inline-block;
}
.errorright {
  flex: 1 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.errorcontent {
  padding-left: 25px;
}
.message-icon {
  display: inline-block;
  width: 100%;
}
.errortitle {
  color: var(--color-primary);
  font-size: 25px;
}
.errormsg {
  color: var(--third-color);
  font-size: 18px;
  line-height: 30px;
  margin: 30px 0;
}
@media only screen and (max-width: 700px){
  .error404 {
    width: 100%;
  }
}
@media only screen and (max-width: 500px){
  .error404 {
    display: block;
  }
  .errorcontent {
    padding-left: 0;
  }
  .errorleft {
    margin-bottom: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .message-icon {
    height: 220px;
  }
}