.hero {
    background: var(--color-secondary);
    border-radius: 10px;
    padding: 10px;
    display: flex;
}
.bigart {
    flex: 1 45%;
    padding: 8px;
}
.smallart {
    display: flex;
    flex-wrap: wrap;
    flex: 1 55%;
    padding: 6px;
}

@media only screen and (max-width: 890px){
    .hero {
        display: block;
        padding: 4px;
    }
}
@media only screen and (max-width: 400px){
    .smallart {
        display: none;
    }
}