.articles-pages {
    margin-top: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.pageslist {
    width: max-content;
    display: flex;
}
.pageslist li {
    margin: 0 6px;
}
.pageslist li a {
    padding: 12px 14px;
    background: var(--color-primary);
    color: var(--color-light);
    border-radius: 6px;
}