.populardiv {
    display: flex;
    border-radius: 8px;
    margin-bottom: 20px;
    transition: 0.3s;
    cursor: pointer;
}
.populardiv:hover {
    opacity: 0.8;
}
.popimg {
    width: 130px;
    height: 98px;
    flex-shrink: 0;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 8px;
    margin-right: 10px;
}
.popimg img {
    transition: all .3s linear;
    border-radius: 0;
    object-fit: cover;
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.poptitle {
    color: var(--color-primary);
    font-size: 16px;
    margin-bottom: 5px;
    line-height: 18px;
}
.popdate {
    color: var(--third-color);
    display: flex;
    line-height: 20px;
}
.popdate h4 {
    margin-left: 8px;
    font-weight: 200;
}
.popinfo {
    width: 100%;
}