.mightarticle {
    background: var(--third-color);
    padding: 14px;
    border-radius: var(--border-radius);
    flex: 1 33%;
    cursor: pointer;
    transition: 0.3s;
}
.mightarticle:hover {
    opacity: 0.85;
    background: var(--color-primary);
}
.mightarticle:nth-child(2) {
    margin: 0 8px;
}
.mightimg {
    width: 100%;
    height: 150px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: var(--border-radius);
}
.mightimg img {
    transition: all .3s linear;
    border-radius: 0;
    object-fit: cover;
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.mightinfo {
    margin: 12px 0;
    color: var(--color-light);
}
.mightdate, .mightsource {
    display: flex;
    align-items: center;
}
.mightdate {
    margin-bottom: 10px;
}
.mighticon {
    margin-right: 10px;
}
.mighttitle {
    margin-top: 15px;
}
.mighttitle h1 {
    color: var(--color-light);
    font-size: 16px;
    line-height: 26px;
}
:is(.mightdate,.mightsource) h4 {
    font-weight: 200;
}

@media only screen and (max-width: 500px) {
    .mightarticle {
        display: block;
    }
    .mightimg {
        margin-bottom: 20px;
    }
    .mightarticle:nth-child(2) {
        margin: 15px 0;
    }
}