:root {
    --body-background: #fff;
    --color-primary: #6c5ce7;
    --color-primary-darkest: #403789;
    --color-secondary: #e6ebf1;
    --color-light: #fff;
    --third-color: #9099a5;

    --logo-bg: url(/src/assets/images/logolight.png);

    --font-weight-light: 300;
    --font-weight-normal: 600;
    --font-weight-bold: 700;
    --border-radius: 10px;
}
[data-theme='dark'] {
    --body-background: #192a56;
    --color-primary: #fff;
    --color-primary-darkest: #b7b7b7;
    --color-secondary: #273c75;
    --color-light: #6c5ce7;
    --third-color: #b4abfb;

    --logo-bg: url(/src/assets/images/logodark.png);
}
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;700&display=swap');

* {
    font-family: 'Josefin Sans', sans-serif;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

a {
    text-decoration: none;
}
ul {
    list-style: none;
}
body {
    background: var(--body-background);
}
body, header, .hero, .newarticles, .FunctionSecDiv, footer {
    transition: 0.3s ease-in-out;
}