
.skeleton {
    animation: skeleton-loading 1s linear infinite alternate;
    border-radius: 8px;
}
.skeleton-img {
    height: 220px;
}
.skeleton-text-primary {
    width: 100%;
    height: 25px;
    margin-bottom: 5px;
    border-radius: 5px;
}
.skeleton-text-secondary {
    width: 100%;
    height: 15px;
    opacity: .4;
    margin-bottom: 5px;
    border-radius: 5px;
}
.skeletonButton {
    height: 35px;
    width: 180px;
}
.skeletonlast {
    width: 80%;
    margin-bottom: 20px;
}
.skeleton-buttonicon {
    height: 40px;
    margin: 4px;
}
.skeletoninfos {
    height: 25px;
    width: 120px;
    opacity: .5;
}
.skeleton-last {
    width: 70%;
}
.date {
    margin: 0 10px;
}

@keyframes skeleton-loading {
    0% {
        background-color: hsl(200, 20%, 70%);
    }
    100% {
        background-color: hsl(200, 20%, 95%);
    }
}