.smallartcontent {
    margin: 2px;
    flex: 1 33%;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: flex-end;
    background: #fff;
}
.smallartcontent::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: linear-gradient(transparent, #000);
    z-index: 1;
    transition: 0.3s;
}
.smallartcontent img {
    width: 110%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    left: -20px;
    z-index: 0;
    transition: 0.4s;
}
.smallartcontent:hover img {
    height: 106%;
    width: 112%;
}
.smallartcontent:hover::before {
    background: linear-gradient(transparent, #100946);
}
.sdes {
    color: #9b9b9b;
    font-size: 15px;
    margin-left: 10px;
    line-height: 20px;
}
.sartitle {
    color: #fff;
    font-size: 19px;
    margin-bottom: 5px;
    line-height: 24px;
}
.smallinfo {
    z-index: 10;
    padding: 10px;
    padding-bottom: 15px;
    margin-top: 60px;
    width: 100%;
}

@media only screen and (max-width: 600px){
    .smallinfo {
        margin-top: 80px;
    }
    .sartitle {
        font-size: 16px;
        margin-bottom: 5px;
        line-height: 19px;
    }
    .sdes {
        font-size: 14px;
        margin-left: 6px;
        line-height: 17px;
    }
}


/********************************* Large Article Style *********************************/

.largearticle {
    border-radius: 8px;
    height: 100%;
    position: relative;
    overflow: hidden;
    display: flex;
    align-items: flex-end;
    transition: 0.4s;
    background: #fff;
}
.largearticle::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: linear-gradient(transparent, #000);
    z-index: 1;
    transition: 0.6s;
}
.largearticle img {
    width: 110%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    z-index: 0;
    transition: 0.4s;
}
.largearticle:hover img {
    height: 106%;
    width: 112%;
}
.largearticle:hover::before {
    background: linear-gradient(transparent, #100946);
}
.largeinfo {
    z-index: 10;
    padding: 14px;
    padding-bottom: 30px;
    width: 100%;
}
.artitle {
    color: #fff;
    margin-bottom: 8px;
    font-size: 24px;
    line-height: 30px;
}
.des {
    color: #9b9b9b;
    margin-left: 15px;
    line-height: 20px;
}

@media only screen and (max-width: 890px){
    .hero {
        display: block;
    }
    .largeinfo {
        margin-top: 200px;
    }
}
@media only screen and (max-width: 600px){
    .largeinfo {
        margin-top: 160px;
    }
    .artitle {
        font-size: 20px;
        line-height: 24px;
    }
    .des {
        margin-left: 10px;
        line-height: 19px;
        font-size: 14px;
    }
}