.articledetails {
    width: 100%;
    background: var(--color-secondary);
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 20px;
}
.articletitle {
    background: var(--color-primary);
    color: var(--color-light);
    border-radius: 8px;
    padding: 15px 20px;
}
.articletitle h1 {
    width: 100%;
    text-align: center;
    font-size: 26px;
    line-height: 40px;
}
.articleinfos {
    display: flex;
    justify-content: center;
}
.infocontent {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px 7px 20px;
    background: var(--color-primary-darkest);
    color: var(--color-light);
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
    margin: 0 20px;
}
.infoitem {
    display: flex;
    line-height: 20px;
    margin: 0 10px;
}
.infoicon {
    margin-right: 6px;
}
.infocontent h4 {
    font-weight: 100;
}


.articleimage {
    width: 100%;
    height: 370px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 8px;
    margin: 30px 0;
    box-shadow: 0 0 16px 0 rgba(0, 0, 0, 0.15);
}
.articleimage img {
    transition: all .3s linear;
    border-radius: 0;
    object-fit: cover;
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.articlesummary p {
    font-size: 18px;
    line-height: 35px;
    color: var(--color-primary-darkest);
}


@media only screen and (max-width: 600px) {
    .articletitle h1 {
        font-size: 22px;
    }
    .infocontent h4 {
        font-size: 14px;
    }
    .articlesummary p {
        font-size: 16px;
    }
    .shareon {
        display: block;
    }
}
@media only screen and (max-width: 500px) {
    .articletitle h1 {
        font-size: 20px;
    }
    .infocontent {
        padding: 0 10px;
        justify-content: center;
    }
    .infoitem {
        margin: 8px;
    }
}