.pageTitle {
    margin-top: 60px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.pageTitle h4 {
    width: 100%;
    text-align: right;
    font-size: 17px;
    color: var(--third-color);
    font-weight: 200;
}
.sort {
    background: var(--color-primary);
    color: var(--color-light);
    border: none;
    padding: 8px 14px;
    border-radius: 5px;
    cursor: pointer;
}
.sort:focus-visible {
    outline: none;
}