.titlediv {
    width: 100%;
}
.section-title {
    color: var(--color-primary);
    font-size: 26px;
    font-weight: var(--font-weight-bold);
    width: max-content;
}
.section-title::after {
    content: '';
    display: block;
    width: 75%;
    height: 3px;
    background: var(--color-primary);
    margin-top: 5px;
    border-radius: 5px;
}