.darkscroll {
    position: fixed;
    right: 20px;
    bottom: 20px;
    background: var(--color-primary);
    border-radius: 8px;
    height: 90px;
    width: 47px;
    box-shadow: 0 0 8px 0 rgb(0 0 0 / 10%);
    overflow: hidden;
    z-index: 1000000;
}
.darkscroll hr {
    background: none;
    border: none;
    background: var(--color-light);
    height: 1px;
}
.ds {
    width: 100%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: 0.3s;
    border: none;
    background: none;
}
.ds svg {
    color: var(--color-light);
    width: 16px;
    height: 16px;
}
.ds:hover {
    background: var(--color-light);
}
.ds:hover svg {
    color: var(--color-primary);
}
.darkmode {
    position: relative;
}
.darkmode input {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer;
    transition: 0.3s;
}
.darkmode input:hover .darkmode:hover {
    background: var(--color-primary);
}
.darkmode input:hover .darkmode:hover svg {
    color: var(--color-light);
}