.article {
    background: var(--color-secondary);
    border-radius: var(--border-radius);
    display: flex;
    align-items: center;
    padding: 15px;
    margin-bottom: 10px;
}
.articleimg {
    width: 290px;
    height: 220px;
    flex-shrink: 0;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    border-radius: 8px;
    margin-right: 15px;
    transition: 0.3s;
}
.articleimg img {
    transition: all .3s linear;
    border-radius: 0;
    object-fit: cover;
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.articleinfo {
    width: 100%;
}
.articleinfo h1 {
    color: var(--color-primary);
    line-height: 28px;
    font-size: 22px;
    margin-bottom: 10px;
}
p.articledes {
    line-height: 19px;
    margin-bottom: 8px;
    color: #726f89;
}
.overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: linear-gradient(transparent, #403789 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 100;
    transition: 0.3s;
    opacity: 0;
}
.overicondiv {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    border: 3px solid #fff;
    display: flex;
    align-items: center;
    justify-content: center;
}
.overlay svg {
    color: #fff;
    width: 28px;
    height: 28px;
}

.articleimg:hover .overlay {
    opacity: 1;
}

.articleimg:hover img {
    transform: rotate(3deg);
    width: 105%;
    top: -8px;
    left: -7px;
    height: 108%;
}

@media only screen and (max-width: 600px){
    .article {
        display: block;
    }
    .articleimg {
        margin-right: 0;
        margin-bottom: 20px;
        width: 100%;
        height: 240px;
    }
    .articleinfo {
        text-align: center;
    }
}