.notfound {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;    
}
.nfcontent {
    margin: 20px 0;
}
.nfcontent h1 {
    width: 100%;
    text-align: center;
    color: var(--color-primary-darkest);
    font-size: 30px;
    margin-bottom: 10px;
}
.nfcontent p {
    width: 100%;
    text-align: center;
    color: var(--third-color);
    font-size: 17px;
    margin-bottom: 25px;
}

.errimg {
    max-width: 180px;
    max-height: 180px;
}
.errimg img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}