.navbar ul {
    display: flex;
}

@media only screen and (max-width: 700px){
    .navbar {
        position: absolute;
        display: block;
        margin: 0;
        top: 70px;
        left: 0;
        width: 100%;
        height: 0px;
        background-color: var(--color-primary);
        padding: 0;
        transition: 0.3s;
        text-align: center;
        z-index: 100;
    }
    .navbar ul {
        height: 0;
        display: none;
    }
    .navbar.active {
        display: block;
        height: fit-content;
        padding: 30px;
    }
    .navbar.active {
        top: 70px;
    }
    .navbar.active ul {
        display: block;
        height: fit-content;
    }
}