
.navbar ul li {
    margin: 0 15px;
}
.navbar ul li a {
    color: var(--color-primary);
    opacity: 1;
    transition: 0.3s;
}
.navbar ul li a:hover {
    opacity: 0.7;
}

@media only screen and (max-width: 700px){
    .navbar.active ul li {
        margin-bottom: 40px;
    }
    .navbar.active ul li:last-child {
        margin: 0;
    }
    .navbar.active ul li a {
        color: var(--color-light);
    }
}