.contactusdiv {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 40px 0;
    padding: 10px 10px 10px 40px;
    background: var(--color-secondary);
    border-radius: 10px;
}
.cnleft {
    flex: 1 55%;
}
.cnright {
    flex: 1 45%;
}
.cntitle {
    font-size: 2.3rem;
    font-weight: 700;
    line-height: 1;
    color: var(--color-primary);
}
.topse p {
    color: var(--third-color);
    font-weight: 300;
    margin-top: 15px;
    width: 65%;
    margin-bottom: 20px;
    line-height: 25px;
}
.topse {
    margin-bottom: 60px;
}
.middlese {
    margin-bottom: 50px;
}
.btmse {
    margin-top: 20px;
}
.email,
.phone,
.insta {
    display: flex;
    margin-bottom: 10px;
}
.mdicon {
    color: var(--color-primary);
    font-size: 16px;
}
.contactsocial {
    display: flex;
    align-items: center;
}
.sicon {
    background: var(--color-primary);
    color: var(--color-light);
    height: 35px;
    width: 35px;
    border-radius: 6px;
    margin-right: 10px;
    box-shadow: 0 0 10px 0 rgb(0 0 0 / 10%);
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 1;
    transition: 0.3s;
}
.sicon:hover {
    opacity: 0.75;
}
.txt {
    display: flex;
    align-items: center;
    margin-left: 15px;
}
.txt h5 {
    color: var(--color-primary);
    font-size: 16px;
}
i.fas.fa-phone {
    transform: scaleX(-1);
}
.contactus {
    margin: 10px;
    padding: 35px 25px;
    background: var(--color-light);
    border-radius: 10px;
    box-shadow: 0 0 10px 0 rgba(0,0,0,0.12);
}
.inputgroup {
    position: relative;
    width: 100%;
    height: 2.3rem;
    margin-bottom: 24px;
}
.form__input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid var(--third-color);
    border-radius: 0.5rem;
    font-size: 16px;
    color: var(--color-primary);
    outline: none;
    padding: 1.25rem;
    background: none;
    
}
.form__input:hover {
    border-color: var(--color-primary);
}
.form__input:focus {
    border-color: var(--color-primary);
}
.textarea {
    height: 130px;
}
textarea.form__input {
    resize: none;
    height: 100%;
}
.form__label {
    font-size: 16px;
    position: absolute;
    left: 1rem;
    top: 12px;
    padding: 0 0.5rem;
    color: var(--third-color);
    cursor: text;
    transition: top 200ms ease-in, left 200ms ease-in, font-size 200ms ease-in;
    background-color: var(--color-light);
    line-height: 21px;
}
.form__input:focus ~ .form__label,
.form__input:not(:placeholder-shown).form__input:not(:focus) ~ .form__label {
    top: -0.5rem;
    font-size: 14px;
    left: 0.8rem;
    color: var(--color-primary);
}
.submitbtn {
    background: var(--color-primary);
    border: none;
    color: var(--color-light);
    font-size: 16px;
    padding: 10px 35px;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: 0.3s;
}
.submitbtn:hover {
    background: var(--color-primary-darkest);
}
.alert {
    font-size: 14px;
    color: #ff4757;
    margin-bottom: 7px;
    margin-left: 15px;
    transition: 0.3s;
}
.alert.active {
    margin-top: -10px;
}
.last {
    margin-bottom: 15px;
}


@media only screen and (max-width: 860px){
    .cnleft {
        flex: 1 45%;
    }
    .cnright {
        flex: 1 55%;
    }
}
@media only screen and (max-width: 720px){
    .contactusdiv {
        display: block;
        padding: 30px 20px !important;
    }
    .contactus {
        margin: 0;
        padding: 22px 15px;
    }
    .cnright {
        margin-top: 40px;
    }
}