.logo {
    flex: 1;
}
.logo a {
    display: inline-block;
    opacity: 1;
    height: 100%;
    display: flex;
    align-items: center;
    transition: 0.3s;
}

.logo a:hover {
    opacity: 0.8;
}

.logoimg {
    display: inline-block;
    width: 135px;
    height: 40px;
    background: var(--logo-bg);
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
}
@media only screen and (max-width: 500px){
    .logoimg {
        width: 125px;
        height: 30px;
    }
}