.ButtonIcon {
    margin: 4px;
    display: flex;
    align-items: center;
    border-radius: 4px;
    overflow: hidden;
    cursor: pointer;
    transition: 0.3s;
    background: var(--color-primary);
}
.ButtonIcon:hover {
    opacity: 0.75;
}
.buttontxt {
    flex: 1;
}
.buttontxt h4 {
    color: var(--color-light);
    text-align: left;
    margin-left: 10px;
    font-size: 15px;
    font-weight: var(--font-weight-normal);
    text-transform: capitalize;
}
.btnicon {
    padding: 10px;
    color: var(--color-light);
    background: #ffffff36;
}