.footer {
    background: var(--color-secondary);
    padding: 60px  30px 20px 30px;
}
.copyright {
    text-align: center;
    color: var(--color-primary-darkest);
}
.copyright a {
    color: var(--color-primary);
    font-weight: 900;
}
.socialdv {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}
.socialm {
    display: flex;
}
.fb, .twit, .inst {
    height: 35px;
    width: 35px;
    margin: 0 5px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color-light);
    cursor: pointer;
    transition: 0.3s;
    background: var(--color-primary);
}
.fb:hover, .twit:hover, .inst:hover {
    opacity: 0.7;
}
.boxs {
    margin-bottom: 60px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

@media only screen and (max-width: 700px){
    .boxs {
        display: block;
    }
    .linksbox {
        margin-bottom: 40px;
    }
}