.linksbox {
    flex: 1 1 33%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.lbox {
    width: max-content;
}
.boxtitle {
    color: var(--color-primary);
    margin-bottom: 25px;
}
.boxtitle::after {
    content: '';
    display: block;
    width: 75%;
    height: 3px;
    background-color: var(--color-primary);
    margin-top: 6px;
    border-radius: 8px;
}
.links {
    list-style: circle;
    margin-left: 16px;
}
.links li {
    color: #736da1;
    margin-bottom: 10px;
}
.links li a {
    color: #736da1;
    transition: 0.3s;
}
.links li a:hover {
    color: var(--color-primary);
}