.btn {
    background: var(--color-primary);
    color: var(--color-light);
    border: none;
    padding-top: 2px;
    width: 140px;
    height: 35px;
    font-size: 14px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
}
.btn:hover {
    background: var(--color-light);
    color: var(--color-primary);
    box-shadow: 0 0 8px 0 rgb(0 0 0 / 8%);
    border: 1px solid var(--color-primary);
}